import React from 'react';
import { connect } from "redux-zero/react";
import {play,stop,next} from './actions';
import './App.css';



const SingleSong =({song,i}) =>{
  
    return(
      <li key={i} className={song.isPlaying===true?"current":''}>
        {song.name} - {song.artist}
        <span className="duration">{song.duration}</span>
      </li>
    );
}
const ListSong =({songs})=>{
  return(
    <ol>
      {
        songs.map((item,index)=>{
          return <SingleSong key={index} song={item} index={index}/>
        })
      }
  </ol>
  )
}
const App =({songs,nowPlaying})=> {
  
    return (
      <div>
      <h1>Treetunes</h1>
      <ListSong songs={songs}/>
      <button id="play" onClick={play}>Play</button>
      <button id="next" onClick={next}>Next</button>
      <button id="stop "onClick={stop}>Stop</button>
  
    </div>
    );
  }

  const mapToProps = ({songs,nowPlaying}) => ({songs,nowPlaying});
  
  export default connect(mapToProps)(App);



