import store from './store';

export const play=()=>{
    let newPlaylist = [...store.getState().songs];
    newPlaylist[store.getState().nowPlaying].isPlaying = true;

    store.setState({
        songs : newPlaylist,
    })
}
export const stop=()=>{
    let newPlaylist = [...store.getState().songs];
    newPlaylist[store.getState().nowPlaying].isPlaying = false;

    store.setState({
        songs : newPlaylist,
    })
}
export const next=()=>{
    stop();
    let count = store.getState().nowPlaying + 1;
    if(count === store.getState().songs.length){
        count = 0
    }
    store.setState({
        nowPlaying : count,
    })
    play();
}



